<template>
    <b-row>
        <b-col 
            v-if="isLoading"
            md="12" offset-md="0"
        >
            <h6>Loading</h6> <preloader size="40px"></preloader>
        </b-col>
        <b-col v-if="!isLoading" cols="12">
            <h3>{{ title }}</h3>
            <b-table :items="items" :fields="fields" striped>
                <template slot="_delete" slot-scope="data">
                    <b-button
                        v-if="!isDeletingItems[data.item.id]"
                        variant="link"
                        class="py-0 delete-button"
                        @click="onDelete(data.item.id)"
                    >
                        <i class="fa fa-trash"></i>
                    </b-button>
                    <preloader v-if="isDeletingItems[data.item.id]" size="20px"></preloader>
                </template>
                <template slot="_edit" slot-scope="data">
                    <b-button
                        variant="link"
                        class="py-0 edit-button"
                    >
                        <router-link
                            class="add-button fa fa-edit"
                            :to="{ path: relativePath + data.item.id + '/edit' }"
                        ></router-link>
                    </b-button>
                </template>
                <template v-for="link in moreInfoLinks" :slot="'_'.concat(link)" slot-scope="row">
                    <b-button
                        :key="link"
                        variant="link"
                        class="py-0 delete-button"
                        @click.stop="onMoreInfo(row, link)"
                    >
                        <i class="fa fa-info"></i>
                    </b-button>
                </template>
              
                <template slot="row-details" slot-scope="row">
                    <!--Create new subtable button-->
                    <div class="text-align-left">
                        <router-link
                            class="link"
                            :to="{ path: relativePath + row.item.id + currentMoreInfoLink + 'add' }"
                        >
                            <b-button variant="primary" class="mb-2 px-4">
                                Create new {{currentAdditionalInfoLabel}}<i class="fa fa-plus ml-2"></i>
                            </b-button>
                        </router-link>
                    </div>
                    <!--Subtable-->
                    <div v-if="additionalInfoIsLoading" class="text-align-left">
                        Loading <preloader size="20px" class="ml-2"></preloader>
                    </div>
                    <b-table
                        :show-empty="true"
                        v-if="!additionalInfoIsLoading"
                        :items="row.item.additionalInfo"
                        :fields="additionalInfoFields"
                    >
                        <template slot="_edit" slot-scope="data">
                            <b-button
                                variant="link"
                                class="py-0 edit-button"
                            >
                                <router-link
                                    v-if="currentRow"
                                    class="add-button fa fa-edit"
                                    :to="{ path: relativePath + currentRow.item.id + currentMoreInfoLink + data.item.id + '/edit'}"
                                ></router-link>
                            </b-button>
                        </template>
                        <template slot="_delete" slot-scope="data">
                            <b-button
                                v-if="!isDeletingAdditionalItems[data.item.id]"
                                variant="link"
                                class="py-0 delete-button"
                                @click="onDeleteAdditionalInfo(data.item.id, row)"
                            >
                                <i class="fa fa-trash"></i>
                            </b-button>
                            <preloader v-if="isDeletingAdditionalItems[data.item.id]" size="20px"></preloader>
                        </template>
                    </b-table>

                    <b-button size="sm" @click="row.toggleDetails">Hide Details</b-button>
                </template>
            </b-table>
            <router-view></router-view>
        </b-col>
    </b-row>
</template>
<script>
import Backend from '../../services/backend';
import Preloader from '../Preloader';

const backend = new Backend();

export default {
    name: 'TemplateForGetPages',
    data() {
        return {
            items: [],
            isDeletingItems: {},
            isDeletingAdditionalItems: {},
            isLoading: false,
            fields: [],
            additionalInfoFields: [],
            lastShowedKey: '',
            currentRow: null,
            currentMoreInfoLink: '',
            currentAdditionalInfoLabel: '',
            additionalInfoIsLoading: false
        };
    },
    components: {
        Preloader
    },
    props: [
        'title',
        'link',
        'moreInfoLinks'
    ],
    created() {
        this.loadValues();
    },
    computed: {
        relativePath() {
            return this._props.link.replace(/[^/]*\//, '');
        }
    },
    methods: {
        onDelete(id) {
            this.isDeletingItems = Object.assign({}, this.isDeletingItems, { [id]: true });
            backend.deleteRequest(this._props.link + id)
                .then(() => {
                    this.isDeletingItems = Object.assign({}, this.isDeletingItems, { [id]: null });
                    this.loadValues();
                })
                .catch((error) => {
                    let errorText = error.response.data.error || 'Something went wrong. Please try again later';
                    this.$store.dispatch('addNotification', { text: errorText, variant: 'danger' });
                    this.isDeletingItems = Object.assign({}, this.isDeletingItems, { [id]: null });
                });
        },
        onDeleteAdditionalInfo(id, row) {
            let rowId = row.item.id;
            this.isDeletingAdditionalItems = Object.assign({}, this.isDeletingAdditionalItems, { [id]: true });
            backend.deleteRequest(this._props.link + rowId + '/' + this.currentMoreInfoLink + '/' + id)
                .then(() => {
                    this.isDeletingAdditionalItems = Object.assign({}, this.isDeletingAdditionalItems, { [id]: null });
                    this.loadMoreInfoValues(row);
                })
                .catch((error) => {
                    let errorText = error.response.data.error || 'Something went wrong. Please try again later';
                    this.$store.dispatch('addNotification', { text: errorText, variant: 'danger' });
                    this.isDeletingAdditionalItems = Object.assign({}, this.isDeletingAdditionalItems, { [id]: null });
                });
        },
        onMoreInfo(row, moreInfoLink) {
            if (row.item._showDetails) {
                row.toggleDetails();
                if (this.lastShowedKey === row.field.key) {
                    this.lastShowedKey = '';
                    this.currentRow = null;
                    return;
                }
            }
            this.currentRow = row;
            this.additionalInfoIsLoading = true;
            this.currentAdditionalInfoLabel = (row.field.label || '').toLowerCase();
            this.currentMoreInfoLink = moreInfoLink;
            row.toggleDetails();
            this.lastShowedKey = row.field.key;
            this.loadMoreInfoValues(row);
        },
        loadMoreInfoValues(row) {
            let id = row.item.id;
            backend.getRequest(this.link + id + this.currentMoreInfoLink)
                .then(response => {
                    this.additionalInfoIsLoading = false;
                    let keys = Object.keys(response.data[0]);
                    this.additionalInfoFields = keys.concat('_edit', '_delete');
                    row.item.additionalInfo = response.data;
                })
                .catch(error => {
                    if (error.response) {
                        console.log(error.response.status);
                        this.additionalInfoIsLoading = false;
                    }
                });
        },
        loadValues() {
            this.isLoading = true;
            backend.getRequest(this._props.link)
                .then(response => {
                    let keys = Object.keys(response.data[0]);
                    let links = this.moreInfoLinks.map(value => '_'.concat(value));
                    this.fields = keys.concat(links, '_edit', '_delete');
                    this.items = response.data;
                    this.isLoading = false;
                })
                .catch(error => {
                    if (error.response) {
                        console.log(error.response.status);
                    }
                    this.isLoading = false;
                });
        }
    }
};
</script>

<style scoped>
    .delete-button {
        color: #dc3545;
        line-height: 0;
    }
    .edit-button a {
        color: #6c757d;
        line-height: 0;
    }
    .text-align-left {
        text-align: left;
    }
</style>
